html {
  font-size: 120%;
  height: 100%; }

body {
  height: 100%;
  overflow-x: hidden; }

#root, .App {
  height: 100%; }

p {
  margin: 0px; }

button,
[role="button"] {
  padding: 10px;
  border-radius: 5px;
  border-style: none;
  background-color: #e8e8e8;
  color: black;
  font-family: "Quicksand";
  font-size: 1rem;
  cursor: pointer;
  margin: 5px;
  outline: 0; }
  button:hover,
  [role="button"]:hover {
    background-color: #d8d8d8; }

.subheading {
  font-size: 2rem;
  padding-bottom: 10px;
  text-align: center; }

.miniheading {
  font-size: 1.5rem;
  text-align: center; }

a {
  text-decoration: none; }
  a:hover {
    cursor: pointer; }

@-webkit-keyframes slidefade {
  from {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
    opacity: 0; }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1; } }

@keyframes slidefade {
  from {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
    opacity: 0; }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1; } }

@media (min-width: 300px) {
  html {
    font-size: 35%; } }

@media (min-width: 500px) {
  html {
    font-size: 50%; } }

@media (min-width: 768px) {
  html {
    font-size: 75%; } }

@media (min-width: 1280px) {
  html {
    font-size: 100%; } }



